import "./Card.css";
import React, { useState } from "react";
import hat from "./hat.jpg";
import { FaHeart } from "react-icons/fa";

import { FaTwitter } from "react-icons/fa";

import { FaLinkedinIn } from "react-icons/fa";

// const hat =require('./hat.jpg')

function Card() {
  const [character, setChar] = useState("My Name is Hope");
  const [heartnum, setheartnum] = useState(0);

  const handleSkills = () => {
    setChar("I am a front end developer");
  };

  const handleName = () => {
    setChar("My Name is hope");
  };


   const handleHeart = () => {
     let x = heartnum
     setheartnum(x+1);
   };

   

  return (
    <div className="Card">
      <div className="upper-container">
        <div className="image-container">
          <img src={hat} alt="" height="150px" width="150px" />
        </div>
      </div>

      <div className="lower-container">
        <div className="name_buttons">
          <h1>Hi</h1>
          <h1>{character}</h1>

          <div className="button-info">
            <button onClick={handleName}>Name</button>
            <button onClick={handleSkills}>Skills</button>
          </div>
        </div>

        <div className="lower-lower">
          <div className="likes">
            <button onClick={handleHeart} className="heart">
              <FaHeart />
            </button>
            <span className="number">{heartnum}</span>
          </div>

          <div className="button-media">
            <button className="social1">
              <a href="https://www.linkedin.com/?trk=public_jobs_nav-header-logo">
                <FaLinkedinIn />
              </a>
            </button>
            <button className="social0">
             <a href= "https://twitter.com" ><FaTwitter /></a>
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Card;
